import axios from 'axios';

const axiosInstance = axios.create({
  baseURL: process.env.NEXT_PUBLIC_API_BASE_URL || 'http://localhost:8000', // Cấu hình URL cơ sở cho các request API
  timeout: 20000, 
  headers: {
    'Content-Type': 'application/json',
  
  },
});

// Interceptor cho request
axiosInstance.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token'); // Hoặc lấy token từ cookie, sessionStorage nếu cần
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`; // Thêm token vào header Authorization nếu có
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Interceptor cho response
axiosInstance.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    // if (error.response) {
    //   if (error.response.status === 4) {
    //     localStorage.removeItem('token');
    //     window.location.href = '/login'; 
    //   }
    // }
    return Promise.reject(error);
  }
);

export default axiosInstance;
